import React, { useState } from 'react';
import cssVar from 'views/mobileView/styles';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { AiOutlineRight } from 'react-icons/ai';
import TopPannel from './TopPanel';
import { toast } from 'react-toastify';
import AdminServices from 'services/api/AdminServices';
import { RiskFormat } from 'views/mobileView/components/atoms/RiskFunctions';
import Modal from 'react-bootstrap/Modal';
import PauseMFModal from 'views/mobileView/components/molecules/Cards/PausedMFModal';

export default function ProfileDetails(props) {
    const [user,setUser]=useState({})
    const [clientData, setClientData] = useState({});
    const [loading, setLoading] = useState();
    const [showDeleteModal, setShowDeleteModal] = useState(false);
    const [showPausedMfModal, setShowPausedMFModal] = React.useState(false)

    React.useEffect(() => {
        setLoading(true)
        try {
            db.get(db.model.user).then(async (user) => {
                setUser(user.userData)
                await getClientData(user.userData.id)
                setLoading(false)
            })
        } catch (error) {
            toast.error(error);
            setLoading(false)
        }
    }, [])

    const getClientData = async (id) => {
        await db.get(db.model.clientData).then(async (p) => {
            if (p === null) {
                let res = await AdminServices.getClient(id).then(res => res);
                if (res.type === true) {
                    const a = JSON.parse(res.data);
                    let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
                    console.log("Get Client API :", aa)
                    setClientData(aa)
                    db.set(db.model.clientData, JSON.stringify(aa))
                }
            } else {
                setClientData(p)
            }
        })
    }

    const onDelete = async() =>{
        var a = await AdminServices.deactivateAccount(user.id).then(res => res)
        console.log(a)
        if(a.type){
            db.clear();
            props.history.push('/signup')
        } else {
            toast.error("Something went wrong! Please try again.")
        }
    }

    const DeleteModal = () => {
		return (
		  <Modal
			className="bottom-drawer"
			show={showDeleteModal}
			onHide={() => setShowDeleteModal(false)}
			keyboard={false}
		  >
			<Modal.Body>
			  	<div className='header2dark'>Are your sure you want to delete your mintbox account?</div>
                <div className='header2dark' style={{marginTop:'4%',color:cssVar.getPropertyValue('--red')}}>Note: this action is irreversible.</div>
			</Modal.Body>
			<Modal.Footer>
			  <button className="outlineButton" onClick={() => setShowDeleteModal(false)}>
				Close
			  </button>
              <button className="outlineButton" style={{color:cssVar.getPropertyValue('--red'), border:"1px solid "+cssVar.getPropertyValue('--red')}} onClick={() => onDelete()}>
				Confirm
			  </button>
			</Modal.Footer>
		  </Modal>
		)
	  }

    return (
        <div className='mainContainer'>
            <MainHeader header='Profile' navigation={props.history} />
            <div className='dataContainer px-0'>
                <TopPannel />
                <div className='menu-card'>
                    <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_user.png' className='menu-card-icon' />
                    <div className='menu-card-body'>
                        {
                            clientData?.RiskInfo?.length > 0 ?
                                <>
                                    <div className='bodyTextDark'>Your risk level is {RiskFormat(clientData?.RiskInfo?.[0]?.RiskScore) || 'unknown'}</div>
                                    <div className='smallTextLight'>We send annual reminders to update your risk level. Contact us at support@mintbox.ai if you believe your present risk level needs an update</div>
                                    <div className='header3dark secondaryGreen' onClick={() => { props.history.push('/risk-profile') }}>
                                        Take risk assessment test <AiOutlineRight size={12} />
                                    </div>
                                </>
                                :
                                <>
                                    <div className='bodyTextDark'>Your risk level is unknown</div>
                                    <div className='smallTextLight'>Answer a few questions to get an estimate of your risk level. This will help us in recommending you the right investment options.</div>
                                    <div className='header3dark secondaryGreen' onClick={() => { props.history.push('/risk-profile') }}>
                                        Take risk assessment test <AiOutlineRight size={12} />
                                    </div>
                                </>
                        }

                    </div>
                </div>
                {
                    clientData?.SmallcaseInfo?.length ?

                        <div className='menu-card'>
                            <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_user.png' className='menu-card-icon' />
                            <div className='menu-card-body'>
                                <div className='bodyTextDark'>Your broker is '{clientData.SmallcaseInfo[0].Broker}'</div>
                            </div>
                        </div>
                        :
                        <></>
                }
                <div className='menu-card' onClick={() => {
                    // if (clientData?.Client?.[0].IsMFDEnabled === 1) {
                    //     props.history.push('/menu/mf-account-details')
                    // } else {
                    //     props.history.push('/activate-direct-mf/get-started')
                    // }
                    setShowPausedMFModal(true)
                }}>
                    <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/menu_user.png' className='menu-card-icon' />
                    <div className='menu-card-body'>
                        <div className='bodyTextDark'>Mutual fund account details</div>
                        {
                            clientData?.Client?.[0].IsMFDEnabled === 1 ?
                                <></>
                                :
                                <>
                                    <div className='smallTextLight'>Your mutual fund account is still inactive. Activate it now to start investing in commission free direct MF
                                    </div>
                                    <div className='header3dark secondaryGreen'>
                                        Activate mutual funds <AiOutlineRight size={12} />
                                    </div>
                                </>
                        }
                    </div>
                </div>
                <div className='menu-card' onClick={() => setShowDeleteModal(true)} style={{ marginBottom: '4%' }}>
                <img src='https://d3etfstcuyoos5.cloudfront.net/app-images/web-icons/user.svg' className='menu-card-icon redFilter' />
                    <div className='menu-card-body'>
                        <div className='bodyTextDark' style={{ color: cssVar.getPropertyValue('--red') }}>Deactivate mintbox Account</div>
                    </div>
                </div>
            </div>
            <DeleteModal/>
            <PauseMFModal navigation={props.history} showModal={showPausedMfModal} onClose={()=>setShowPausedMFModal(false)}/>
        </div>
    )
}
